.textImage {
  $root: &;
  display: block;
  padding: 30px 0;

  &__inner {
    @media (min-width: 600px) {
      display: flex;
      align-items: flex-start;

      #{$root}--reverse & {
        flex-direction: row-reverse;
      }
    }
  }

  &__content {
    padding-bottom: 20px;

    @media (min-width: 600px) {
      width: 60%;
      padding-right: 24px;
      padding-bottom: 0;

      #{$root}--reverse & {
        padding-right: 0;
        padding-left: 24px;
      }
    }
  }

  &__title {
    font-weight: 300;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 32px;
    line-height: 1.2;
    margin-bottom: 1em;
  }

  &__text {
    font-family: 'Times New Roman', Times, serif;
    font-size: 18px;
    line-height: 1.5;
  }

  &__image {
    position: relative;
    display: block;
    width: 100%;

    @media (min-width: 600px) {
      width: 40%;
    }

    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 64%;
      height: 0;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}